<template>
    <v-container :fluid="true">
      <div class="text-center">
          <h2>Fecha de Registro Patología</h2>
          <h3>Fecha: {{FechaActual()}}</h3>
      </div>
        <v-form @submit.prevent="Guardar"   ref="formularioPatologia"   lazy-validation  >
      <v-row>
        <v-col  cols="12" sm="6" md="4" >
           <v-row no-gutters>
           <v-col cols="10" >
          <v-text-field :autofocus="true" :rules="dniRules"  v-model="dni" type="number" 
           :color="color_salud"  label="DNI" dense outlined :background-color="color_lighten_salud" ></v-text-field>
           </v-col>
           <v-col cols="2" :class="{'disable-events':btnObtenerPaciente}">
             <v-btn :disabled="dni.length!==8" class="py-1" block dark color="blue darken-4" @click="obtenerPacientePatologia">
                <v-icon>
                  search
                </v-icon>
             </v-btn>
           </v-col>
          </v-row>       
          <v-text-field :rules="requeridoRules" v-model="nombres"  :color="color_salud"   label="Nombres" dense outlined :background-color="color_lighten_salud" ></v-text-field>
          <v-text-field :rules="requeridoRules" v-model="apellidos"  :color="color_salud" label="Apellidos" dense outlined :background-color="color_lighten_salud" ></v-text-field>
        </v-col>
         <v-col cols="12" sm="6" md="4" >
          <!-- El item-text es el valor del nombre del objeto con el que se buscara en el array object, ES decir si pongo group o apellido buscara por grupo o apellido pero no recnocera nombre
           sino por el valor que ponga ahi
          item-value es el valor que ingresara al array del v-model si es que pongo que sea multiple -->
             <v-text-field :rules="requeridoRules"  v-model="fechaNacimiento" type="date" :color="color_salud" label="Fecha Nacimiento" dense  outlined :background-color="color_lighten_salud"  ></v-text-field>
          <v-text-field  v-model="telefono" type="number"  :color="color_salud" label="Teléfono" dense  outlined :background-color="color_lighten_salud"  ></v-text-field>
         <v-select v-model="empresa" :items="itemsEmpresa" item-text="descripcion" item-value="descripcion" :item-color="color_salud"  :class="color_text_salud" label="Empresa" :color="color_lighten_salud" :background-color="color_lighten_salud" 
          dense  outlined  ></v-select>
          <v-text-field :rules="empresaRules" v-if="empresa==='OTROS'" :autofocus="true"  v-model="nuevaEmpresa" type="text" :color="color_salud"  label="Nueva Empresa" dense  :background-color="color_lighten_salud" ></v-text-field>         
        </v-col>
         <v-col cols="12" sm="6" md="4" >
          <v-select :items="dataTipoMuestraPat" 
          :item-color="color_salud" 
           :class="color_text_salud" 
          label="Tipo de Muestra" 
          :color="color_salud"
          :background-color="color_lighten_salud"  
          item-text="descripcion"
          item-value="idTipoMuestraPat"
          v-model="dataTipoMuestraPatSelected"
          dense  outlined  ></v-select>
         <div class="d-flex flex-row "  v-if="dataMuestraPat.length>0 && dialogInsertarMuestra==false" >
             <v-select :items="dataMuestraPat" 
          :item-color="color_salud" 
           :class="color_text_salud" 
          label="descripcion" 
          :color="color_salud"
          :background-color="color_lighten_salud"  
          item-text="descripcion"
          item-value="idMuestraPat"
          v-model="examen"
          dense  outlined  ></v-select>
            
          
            <v-btn :disabled="examen==='' ?true:false" icon color="white" :class="color_salud" class=" flex-row mx-2" @click="agregarExamen(examen)" ><v-icon>add</v-icon></v-btn>
         </div> 
         <div>
          <v-btn @click="dialogInsertarMuestra=true" dark color="blue darken-4">Nuevo</v-btn>
          </div>          
        </v-col>
   </v-row>
   <h5>Examenes Realizados</h5>
   <p class="font-weight-bold red--text">{{mensajeExamen}} </p>
             <!-- {{examenesPat}}
             {{examenesRealizadosPat}} -->
            <v-row class="ma-0 pa-0" >              
              <v-col cols="12" sm="6" md="4 mb-2" class="ma-0 pa-0" v-for="(item,index) in examenesPat" :key ="index" >
                <v-card  outlined  class="pt-3 mx-1 px-1 blue lighten-2" >
                  <v-btn @click="eliminarExamen(item.id)" class="float-right red" :x-small="true" color="white"  icon> <v-icon>close</v-icon></v-btn>
                  <v-row class="ma-0 pa-0">
                <v-col class="ma-0 pa-0" cols="6">
                <v-switch  dense  v-model="examenesRealizadosPat" :label="item.muestra" :value="item.id" class="ma-0 pa-0"  color="blue darken-4" >  </v-switch>
              </v-col>
              <v-col class="ma-0 pa-0" cols="5">
                 <v-text-field class="ma-0 pa-0" v-model="examenesPat[index].cantidad" type="number"   :color="color_lighten_salud" label="Cantidad" dense outlined :background-color="color_lighten_salud"  ></v-text-field>
              </v-col>  
               </v-row>
                              
                  <v-row v-if="verificarExamenRealizado(item.id,index)" justify="center" class="ma-0 pa-0">

                    <v-row class="ma-0 pa-0 mb-2">
                      
                        <v-checkbox
                        :class="!examenesPat[index].pagado? 'pb-4': ' '"
                        class="ma-0 pa-0"  
                        dense
                        v-model="examenesPat[index].pagado"
                        label="¿Pagado?"
                        color="blue darken-4"
                        :value="!pagado"
                        hide-details
                      ></v-checkbox>
                      
                    </v-row>
                   <v-row class="ma-0 pa-0" v-if="examenesPat[index].pagado">      
                         <v-text-field  v-model="examenesPat[index].fechaEnvio" type="date"  :color="color_salud" label="Fecha de Envio" dense  outlined :background-color="color_lighten_salud"  ></v-text-field>
                    </v-row>
                      <v-row  class="ma-0 pa-0" v-if="examenesPat[index].pagado">
                        <v-col  cols="4" class="ma-0 pa-0">
                          <v-text-field :rules="requeridoRules" v-model="examenesPat[index].nroVoucher"  :color="color_salud" label="Boleta o Factura" dense outlined background-color="blue lighten-4" id="nroVoucherPat" ></v-text-field>
                        </v-col>
                        <v-col class="ma-0 pa-0" cols="1">
                          <v-btn   @click="copiarTexto()"   block dark color="blue darken-4" > <v-icon>content_copy</v-icon></v-btn>
                        </v-col>
                   </v-row>    
                    <v-row class="ma-0 pa-0" v-if="examenesPat[index].pagado">
                      <v-col cols="5" class="ma-0 pa-0">
                          <v-checkbox
                          class="ma-0 pa-0"  
                          dense
                          v-model="examenesPat[index].enviado"
                          label="¿Enviado?"
                          :color="color_salud"
                          :value="!enviado"
                          hide-details
                        ></v-checkbox>
                      </v-col>
                      <v-col  cols="7" class="ma-0 pa-0">
                          <v-text-field v-model="examenesPat[index].fechaResultado" type="date"  :color="color_salud" label="Entrega del Resultado" dense  outlined :background-color="color_lighten_salud"  ></v-text-field>
                    </v-col>
                    </v-row>
                  </v-row>                                
                </v-card>
              </v-col>
            </v-row>
     <v-row justify="center" class="ma-0 pa-0">
     <v-col cols="6" v-if="ambienteDeModificacion==false">
     <v-btn class="px-5"  :class="{'disable-events':btnGuardarDeshabilitado}" block dark color="blue darken-4" type="submit">Guardar</v-btn>
     </v-col>
     <v-col v-else cols="12" class="d-flex">
       <v-row >
         <v-col cols="6"> 
           <v-btn class="px-5 mx-1" :class="{'disable-events':btnGuardarDeshabilitado}" block dark color="blue darken-4"  @click="Modificar()" >Modificar</v-btn>
        </v-col>
         <v-col cols="6">
            <v-btn class="px-5 mx-1 " block dark :color="color_salud" @click="Cancelar()" >Cancelar</v-btn>
         </v-col>
        </v-row>
    </v-col>
  </v-row>
  <v-row justify="center" class="ma-0 pa-0">
      <v-alert v-if="mensajeResultadoTransaccion!==''" dense outlined :type="tipoMensajeResultado? 'success': 'error'">
          {{ mensajeResultadoTransaccion }}</v-alert>
  </v-row>
  </v-form>
  <v-row>
     <v-col cols="11"  class="mx-auto">
      <Tabla @fillDataModificarPat="fillDataToMod"  @limpiarPat="Limpiar" ref="tabla" ></Tabla>
      </v-col>
  </v-row>
  <v-dialog v-model="dialogInsertarMuestra" max-width="290" persistent>
    <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold"> NuevaMuestra </div>
        </v-list-item-content>
        <div class="px-2">
          <v-select :items="dataTipoMuestraPat" 
         :item-color="color_salud" 
           :class="color_text_salud" 
          label="Tipo de Muestra" 
          :color="color_salud" 
          :background-color="color_lighten_salud"  
          item-text="descripcion"
          item-value="idTipoMuestraPat"
          v-model="dataTipoMuestraPatDialog"
          dense  outlined  ></v-select>
        </div>
          <div class="px-2">
          <v-text-field :autofocus="true"  v-model="nuevaMuestra" type="text" :class="color_salud"  label="Nueva Muestra" dense  :background-color="color_lighten_salud" ></v-text-field>      
          </div>
          <v-list-item-content>
              <v-layout mx-2 :justify-center="true">
                <v-btn :disabled="nuevaMuestra===''? true:false"   :class="color_salud" dark @click="addNuevaMuestra(dataTipoMuestraPatDialog,nuevaMuestra)" >Guardar</v-btn>
              </v-layout>
        </v-list-item-content>
          <v-list-item-content>
              <v-layout mx-2 :justify-center="true">
                <v-btn @click="()=>{dialogInsertarMuestra=false,nuevaMuestra=''}" dark>Cerrar</v-btn>
              </v-layout>
        </v-list-item-content>
    </v-card>
  </v-dialog>
   <v-dialog v-model="dialogNoData" max-width="290" persistent>
      <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">
           {{mensajeNoData}}
          </div>
        </v-list-item-content>
        <v-list-item-content>
          <v-layout :justify-center="true">
           
            <v-btn @click="dialogNoData=false" rounded dark>Cerrar</v-btn>
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>
    </v-container>
</template>
<script>
import  {mapState,mapActions} from 'vuex'
import Tabla from '../components/TablaPatologia'
export default {
    name:"Patologia",
    components:{Tabla},
    data() {
        return {
                    color_salud:'blue darken-4',
          color_lighten_salud:'blue lighten-5',
          color_text_salud:'blue--text text-lighten-4',
          btnGuardarDeshabilitado:false,
          btnObtenerPaciente:false,
          dialogNoData:false,
          mensajeNoData:'',
          mensajeResultadoTransaccion:'',
          tipoMensajeResultado:false,
          dialogInsertarMuestra:false,
          nuevaTipoMuestra:false,
          nuevaMuestra:'',
          ambienteDeModificacion:false,
           dniRules: [(v) => (!!v && (v.length==8) )|| "8 Numeros"],
          requeridoRules: [(v) => !!v || "es requerido"],
          empresaRules:[(v) => !!v || "es requerido"],
          telefonoRules:[(v) => (!!v && v.length>8 && v.length<12)|| "cantidad min 9 y 11 max"],
          examen:'',        
          //contenedor de temporal de datos que seran eliminados
          dataTempToDeleteAboutModify:[],
          //esta variable contiene a todos los que estana ctivos o examenes realizados switch True 
          examenesRealizadosPat:[],
          enviado:true,
          pagado:true,
          dataTipoMuestraPat:[],
          dataTipoMuestraPatSelected:0,
          dataTipoMuestraPatDialog:0,
          dataMuestraPat:[],
          itemsEmpresa:[],
          //esta variable contiene TODO LOS DATOS de los examenes que seran registrados omodificados
        examenesPat:[],
        dni:'',
        nombres:'',
        apellidos:'',
        fechaNacimiento:'',
        telefono:'',
         empresa:'',
         nuevaEmpresa:''
      }
    },
    mounted(){
        this.getTipoMuestraPat();
      this.listarEmpresa();
    },
    computed:{
     
      mensajeExamen:function(){
        if(!this.examenesPat.length>0)
            return 'Se require seleccionar Examenes'
            return ''
        },
        ...mapState(['token'])
    },
    watch:{
          dataTipoMuestraPatSelected:function(val){
              if(val>0) this.getMuestraPat(val)
          }},
    methods: {
      ...mapActions(['getPaciente']),
     async obtenerPacientePatologia(){
       //deshabilitamos el div que conteien al boton para no hacer peticiones recurrentes en vano
       this.btnObtenerPaciente= await true;
       this.nombres='';
            this.apellidos='';
            const api =localStorage.getItem('api');
         var dataPaciente= await this.getPaciente({payload:this.dni});

          if(dataPaciente.nombres){
            this.nombres=dataPaciente.nombres;
            this.apellidos=dataPaciente.apellidos;
          }else{
                  if(dataPaciente=="Ha superado la cantidad de consultas mensuales: 250" && api!=2){
                        localStorage.setItem('api',2);
                        this.obtenerPacientePatologia();                     
                      }else{
                        this.mensajeNoData=dataPaciente;
                        this.dialogNoData=true;
                        }
              }
               //habilitamos el div para reliazar peticion por dni
              this.btnObtenerPaciente= await false;
      },

      addNuevaMuestra(id,e){
        this.axios.post('/saveMuestra',{idTipoMuestraPat:id ,descripcion:e})
        .then(res=>{
        this.nuevaMuestra='';
        this.getTipoMuestraPat();
        this.dialogInsertarMuestra=false;
        })
        .catch(err=>console.log(err))
      },
       listarEmpresa(){
        this.axios.get('/listarEmpresa')
        .then(res=>{
          res.data.push({idEmpresa:0,descripcion:'OTROS'});
          this.itemsEmpresa=res.data;
        })
        .catch(err=> console.log(err))
      },
        getTipoMuestraPat(){
          //se resetea ese array cada vez que se agregue una nueva muestra
          this.dataTipoMuestraPat=[];
          this.axios.get('/listarTipoMuestraPat')
          .then(res=>{this.dataTipoMuestraPat=res.data; })
          .catch(err=>{ console.log(err); })
        },
       getMuestraPat(e){
          this.axios.get(`/listarMuestraPat/${e}`)
          .then(res=>{  this.dataMuestraPat=res.data;})
          .catch(err=>{   console.log(err);  })
          },
      FechaActual(){
          const hoy = new Date();
          var año=hoy.getFullYear()
          var mes=(hoy.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=hoy.getDate()
              dia=(dia<10)?('0'+dia):dia;
       return año+'-'+mes+'-'+dia;
      },
      convertidorFecha(str){
          if(str==null){
            return 'Sin Data'
          }else{
            const f = new Date(str);
              var año=f.getFullYear()
              var mes=(f.getMonth() + 1)
                  mes=(mes<10)?('0'+mes):mes;
              var dia=f.getDate()
                  dia=(dia<10)?('0'+dia):dia;
          return año+'-'+mes+'-'+dia;
          }
     },
      Guardar(){
         var headers = {
        "Content-Type": "application/json",
        token: this.token,
       };
        var existeExamen=this.examenesPat.length>0;
        var regPatologia = this.$refs.formularioPatologia.validate();

        if(existeExamen && regPatologia ){
          this.btnGuardarDeshabilitado=true;
          this.axios.post("/saveExamPatologia",{
                  dni:this.dni,
                  nombres:this.nombres.trim(),
                  apellidos:this.apellidos.trim(),
                  fechaNacimiento:this.fechaNacimiento,
                  telefono:this.telefono,
                  empresa:this.nuevaEmpresa===''? this.empresa:this.nuevaEmpresa,
                  fechaRegistroExamen:this.FechaActual(),
                  examenes:this.examenesPat
          },{headers}) 
          .then(res=>{
                this.dni=''
                this.nombres=''
                this.apellidos=''
                this.fechaNacimiento=''
                this.telefono=''
                this.empresa=''
                this.examenesPat=[]
                this.examenesRealizadosPat=[]

                if(res.data.mensaje){
                this.tipoMensajeResultado=true;
                this.mensajeResultadoTransaccion=res.data.mensaje;           
                setTimeout(()=>{
                  this.mensajeResultadoTransaccion=''
                   this.tipoMensajeResultado=false},2000 );}
                this.$refs.tabla.ListarMuestras();
                 this.btnGuardarDeshabilitado=false;
          })
          .catch(err=>{    
                 err.response.data.sqlMessage? this.mensajeResultadoTransaccion=err.response.data.sqlMessage :   this.mensajeResultadoTransaccion=err;            
                setTimeout(()=>{this.mensajeResultadoTransaccion=''},2000 )
                 this.btnGuardarDeshabilitado=false;
            }) }   
      },
      verificarExamenRealizado(ele,i){
            var existe=this.examenesRealizadosPat.includes(ele);
            if(!existe){
                   this.examenesPat[i].atendido=false
                   this.examenesPat[i].pagado=false
                   this.examenesPat[i].enviado=false
                   this.examenesPat[i].fechaResultado=''
                   this.examenesPat[i].fechaEnvio=''}
                  else{
                      this.examenesPat[i].atendido=true
                      this.examenesPat[i].fechaResultado=''
                   }
      return existe;
      },
      agregarExamen(e){
        if(e!==null){
          //este dato sera agregado a la lista de los card examenes
            var examsPat={};
            if(this.ambienteDeModificacion){
              this.dataMuestraPat.filter(ele=>{if(ele.idMuestraPat===e) examsPat={
                  doExamen:'Nuevo',
                    idPaciente:this.examenesPat[0].idPaciente,
                    idExamen:0,id:ele.idMuestraPat,muestra:ele.descripcion,atendido:false,cantidad:1,pagado:false,enviado:false,fechaResultado:'',fechaEnvio:''} });
                }else{
                  this.dataMuestraPat.filter(ele=>{if(ele.idMuestraPat===e) examsPat={id:ele.idMuestraPat,muestra:ele.descripcion,atendido:false,cantidad:1,pagado:false,enviado:false,fechaResultado:'',fechaEnvio:''} });
                }         
                var existe=false;
                this.examenesPat.filter(x=>{ if(x.id===examsPat.id) existe=true })  
            
                  if(!existe)
                    this.examenesPat.push(examsPat);
                  this.examen='' 
        }
      },
      eliminarExamen(e){  
       if(this.ambienteDeModificacion){
          this.examenesPat= this.examenesPat.filter(elem=>{
           if(elem.id==e && elem.doExamen=='Modificar')  { 
             elem.doExamen="Eliminar"
             //agregando al contenedor de eliminacion
             this.dataTempToDeleteAboutModify.push(elem);
              }
              return elem;
              })
       }
          this.examenesPat= this.examenesPat.filter(elem=>{ if(elem.id!==e) {return elem}})
          this.examenesRealizadosPat=this.examenesRealizadosPat.filter(ele=>{if(ele!==e) {return ele}})
     },
      fillDataToMod(d){
         this.dataTempToDeleteAboutModify=[]
        this.ambienteDeModificacion=true;
//Vaciamos par modificar otro dato
          this.examenesPat=[];
          this.examenesRealizadosPat=[];
          this.dni=d.dni
          this.nombres=d.nombres
          this.apellidos=d.apellidos
          this.fechaNacimiento=this.convertidorFecha(d.fechNac)
          this.telefono=d.telefono
          this.empresa=d.empresa
          var iterador=0;
               for (let index = 0; index < d.detalleExamen.length; index++) {         
                   if (!(d.detalleExamen[index].fechaAtencion==null)){
                   //agregando a los examenes realizados si es que tien fecha de atendido
                      this.examenesRealizadosPat[iterador]=d.detalleExamen[index].idMuestraPat
                      iterador++;   }  
                  this.examenesPat.push({
                  doExamen:'Modificar',
                  idPaciente:d.detalleExamen[index].idPaciente,
                  idExamen:d.detalleExamen[index].idExamen,
                  id:d.detalleExamen[index].idMuestraPat,
                  muestra:d.detalleExamen[index].examen,
                  atendido:d.detalleExamen[index].fechaAtencion==null? false:true ,
                  cantidad:1,
                  pagado:d.detalleExamen[index].estadoPago==0? false:true,
                  enviado:d.detalleExamen[index].estadoEnvio==0? false:true ,
                  fechaEnvio:d.detalleExamen[index].fechaEnvioMuestra==null? '':this.convertidorFecha(d.detalleExamen[index].fechaEnvioMuestra) ,
                  fechaResultado:d.detalleExamen[index].fechaEntregaResultado==null? '':this.convertidorFecha(d.detalleExamen[index].fechaEntregaResultado)})
               } 
               iterador=0;
      },
      Modificar(){
           var headers = {
        "Content-Type": "application/json",
        token: this.token
       };
       var eData=this.examenesPat;
       if(this.dataTempToDeleteAboutModify.length>0){
             eData=[...eData,...this.dataTempToDeleteAboutModify]}
        if( eData.length==0){
                  eData=this.dataTempToDeleteAboutModify;}
        var existeExamen=eData.length>0;
        var regPatologia = this.$refs.formularioPatologia.validate();

        if(existeExamen && regPatologia ){
          this.btnGuardarDeshabilitado=true;
          this.axios.post("/modificarExamPatologia",{
         
                  dni:this.dni,
                  nombres:this.nombres.trim(),
                  apellidos:this.apellidos.trim(),
                  fechaNacimiento:this.fechaNacimiento,
                  telefono:this.telefono,
                  empresa:this.nuevaEmpresa===''? this.empresa:this.nuevaEmpresa,
                  fechaRegistroExamen:this.FechaActual(),
                  examenes:eData
          },{headers})
         .then(res=> {
        
                this.$refs.tabla.ListarMuestras();
                this.dni=''
                this.nombres=''
                this.apellidos=''
                this.fechaNacimiento=''
                this.telefono=''
                this.empresa=''
                this.examenesPat=[]
                this.examenesRealizadosPat=[]
                this.dataTempToDeleteAboutModify=[]

                if(res.data.mensaje){
                this.tipoMensajeResultado=true;
                this.mensajeResultadoTransaccion=res.data.mensaje;           
                setTimeout(()=>{
                  this.mensajeResultadoTransaccion=''
                   this.tipoMensajeResultado=false},2000 );}

                this.ambienteDeModificacion=false;  
                this.btnGuardarDeshabilitado=false;
          })
         .catch(err=>{ 
             err.response.data.sqlMessage?this.mensajeResultadoTransaccion=err.response.data.sqlMessage :   this.mensajeResultadoTransaccion=err;            
             setTimeout(()=>{this.mensajeResultadoTransaccion=''},2000 )
             this.btnGuardarDeshabilitado=false;
                }) 
        }
      },
      Cancelar(){
                this.dni=''
                this.nombres=''
                this.apellidos=''
                this.fechaNacimiento=''
                this.telefono=''
                this.empresa=''
                this.examenesPat=[]
                this.examenesRealizadosPat=[]
                this.dataTempToDeleteAboutModify=[]
                this.ambienteDeModificacion=false;
      },
        Limpiar(){
          
             if(this.ambienteDeModificacion) {
                this.dni=''
                this.nombres=''
                this.apellidos=''
                this.fechaNacimiento=''
                this.telefono=''
                this.empresa=''
                this.examenesPat=[]
                this.examenesRealizadosPat=[]
                this.dataTempToDeleteAboutModify=[]

                this.ambienteDeModificacion=false;  
                this.btnGuardarDeshabilitado=false;
             } 
            
        }
    },
}
</script>
