<template>
  <div>
    <v-row class="ma-0">
      <v-col cols="12" md="5" class=" pa-1">
        <v-text-field type="date" v-model="desde"  :color="color_salud" label="Desde" dense outlined background-color="blue lighten-4" ></v-text-field>
      </v-col>
      <v-col cols=10 md="5" class=" pa-1">
        <v-text-field type="date" v-model="hasta"  :color="color_salud" label="Hasta" dense outlined background-color="blue lighten-4" ></v-text-field>
      </v-col>
      <v-col cols="2" md="2" class="ma-0  pa-1">
        <v-btn  class="py-1" block dark  :color="color_salud" @click="ListarMuestras">
                  <v-icon>
                    search
                  </v-icon>
              </v-btn>
        </v-col>
    </v-row>
  

    <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Buscar"
              single-line
              hide-details
              class="mb-2"
            ></v-text-field>

            <v-data-table  
              dense
              fixed-header   
              :headers="dataHeaders"
              :items="PatologiaData"
              :search="search"
              class="elevation-1 table_main"
              :loading="loading"
              loading-text="Cargando.. Espere porfavor"
            >
      
           <template v-slot:[`item.acciones`]="{ item }">
             <v-btn @click="toDataDialog(item.detalleExamen,item.paciente)" icon outlined small class="ma-1">        
                <v-icon>view_list</v-icon>
              </v-btn>            
              <v-btn  icon  outlined small class="ma-1"
                @click="editarDataPat(item)">
                                    <v-icon>edit</v-icon> 
                                </v-btn>
                 <v-btn elevation="3" icon outlined small class="ma-1" 
                 @click="ShowDialogDeletePat(item.examenes, item.paciente,item.id,item.detalleExamen )" >
                 <v-icon>delete</v-icon> 
              </v-btn>
          </template>   
    </v-data-table>



    <v-dialog v-model="dialogDeleteExamPat" class="table_main" max-width="290" persistent>
      <v-card class="py-2">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">
            {{ dataDialogDeletePat.message }}
            <span :class="color_letras_salud" class="text-center  ma-2">
              {{ dataDialogDeletePat.examen }}
            </span>
          </div>
          <div class="font-weight-bold">
            de
            <span :class="color_letras_salud" class="text-center  ma-2">
              {{ dataDialogDeletePat.nombre }}
            </span>
          </div>
        </v-list-item-content>
        <v-list-item-content>
          <v-layout mx-2 :justify-space-between="true">
            <v-btn
              @click="Eliminar(dataDialogDeletePat.data)"
              rounded
              outlined
              :color="color_salud"
              >Si</v-btn
            >
            <v-btn @click="NotShowDialogDeletePat" rounded dark>No</v-btn>
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>
     <v-dialog v-model="dialogDetalleExamPat" class="table_main"  max-width="350" persistent>
      <v-card class="py-2 ">
        <v-list-item-content class="text-center">
          <div class="font-weight-bold">
            {{ dataDialogDetallePat.nombre }}
          </div>
        </v-list-item-content>
        <v-simple-table class="table_main font-weight-medium" dense fixed-header >
          <template v-slot:default>
            <thead >
                <tr>
                <th>
                  Examen
                </th>
                 <th>
                  Cant.
                </th>
                  <th>
                    Registrado
                  </th>
                  <th>
                    Atendido
                  </th>
                  <th>
                    Pagado
                  </th>
                  <th>
                    Enviado
                  </th>
                  <th>
                    Fech.Envio
                  </th>
                  <th>
                    Resultado
                  </th>
                </tr>
            </thead>
              <tbody>
                  <tr v-for="(item,index) in dataDialogDetallePat.data " :key="index">
                    <td class=" text-center">{{ item.examen}}</td>
                    <td class=" text-center">{{ item.cantidad}}</td>
                     <td  :class="{ 'red':item.fechaRegistroExamen==null}" class=" text-center">{{  convertidorFecha (item.fechaRegistroExamen) }}</td>
                     <td :class="{ 'red':item.fechaAtencion==null}" class="text-center">{{ convertidorFecha(item.fechaAtencion )}}</td>
                     <td :class="{ 'red':item.estadoPago==0}" class="text-center">{{item.estadoPago==0 ? 'No':'Si' }}</td>
                     <td :class="{ 'red':item.estadoEnvio==0}" class="text-center">{{item.estadoEnvio==0 ? 'No':'Si' }}</td>
                     <td :class="{ 'red':item.fechaEnvioMuestra==null}"   class="text-center">{{convertidorFecha( item.fechaEnvioMuestra )}}</td>
                     <td :class="{ 'red':item.fechaEntregaResultado==null}"    class="text-center">{{ convertidorFecha(item.fechaEntregaResultado )}}</td>
                  </tr>
              </tbody>
          </template>
        </v-simple-table>
        <v-list-item-content>
          <v-layout mx-2 :justify-center="true">
            <v-btn @click="dialogDetalleExamPat=false" rounded dark>Cerrar</v-btn>
          </v-layout>
        </v-list-item-content>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import {mapState} from 'vuex'
export default {
  name: "TablaPatologia",
  data() {
    return {
        color_salud:"blue darken-3",
       color_letras_salud:"blue-grey--text text-darken-4",
      loading:true,
      dialogDetalleExamPat:false,
      dialogDeleteExamPat: false,
       dataDialogDeletePat: {
        message: "Eliminar",
        id: null,
        examen: null,
        nombre: null,
        data:null
      },
      dataDialogDetallePat: {
        nombre: "",
        data:[]
      },
                search: '',
          dataHeaders:[
                { text:'*',align: 'start',   sortable: false, value: 'index', class:"headerStyleLeft blue darken-2 text-center  font-weight-bold white--text " },
                { text: 'EMPRESA', sortable: false, value: 'empresa',class:" blue darken-2  font-weight-bold white--text" },
                { text: 'DNI', sortable: false, value: 'dni',class:" blue darken-2 text-center  font-weight-bold white--text" },
                { align: 'Center',  sortable: false, text: 'Paciente', value: 'paciente',class:"blue darken-2 text-center  font-weight-bold white--text" },
                { text: 'Examen(Cant.)',  sortable: false, value: 'examenes',class:"blue darken-2 text-center  font-weight-bold white--text" },
                { text: 'Fech. Nac',  sortable: false, value: 'fechNac' ,class:"headerStyleRigtht blue darken-2 text-center  font-weight-bold white--text" },
                { text: 'Telefono',  sortable: false, value: 'telefono' ,class:"headerStyleRigtht blue darken-2 text-center  font-weight-bold white--text" },
                 { text: '',  sortable: false, value: 'acciones' ,class:"headerStyleRigtht blue darken-2 text-center  font-weight-bold white--text" },          
                ],
      PatologiaData: [],
      dataDetalleExam: [],
       desde:'',
       hasta:''
    };
  },
  computed:{
    ...mapState(['token'])
  },
 async mounted() {
   await this.inicializar();
    this.ListarMuestras();
  },
  methods: {
     inicializar(){
        this.desde=this.FechaActual(); 
        this.hasta=this.FechaActual(); 
      },
    FechaActual(){
          const hoy = new Date();
          var año=hoy.getFullYear()
          var mes=(hoy.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=hoy.getDate()
              dia=(dia<10)?('0'+dia):dia;
       return año+'-'+mes+'-'+dia;
      },
    toDataDialog(data,nombre){
      this.dataDialogDetallePat.data=data;
       this.dataDialogDetallePat.nombre=nombre;
      this.dialogDetalleExamPat=true;
    },
    convertidorFecha(str){
      
      if(str==null){
        return 'Sin Data'
      }else{
        const f = new Date(str);
          var año=f.getFullYear()
          var mes=(f.getMonth() + 1)
              mes=(mes<10)?('0'+mes):mes;
          var dia=f.getDate()
              dia=(dia<10)?('0'+dia):dia;
       return dia+'/'+mes+'/'+año;
      }
    },
    ShowDialogDeletePat(exam, name, id,data) {
        this.dataDialogDeletePat.data = data;
      this.dataDialogDeletePat.examen = exam;
      this.dataDialogDeletePat.nombre = name;
      this.dataDialogDeletePat.id = id;
      this.dialogDeleteExamPat = true;
    },
    NotShowDialogDeletePat() {
      this.dialogDeleteExamPat = false;
       this.dataDialogDeletePat.data = null;
      this.dataDialogDeletePat.examen = null;
      this.dataDialogDeletePat.nombre = null;
      this.dataDialogDeletePat.id = null;
    },
    Eliminar(data) {
        var headers = {
        "Content-Type": "application/json",
        token: this.token,
       };
          this.axios.post("/eliminarExamPatologia",data,{headers}) 
          .then(res=>{
             this.$emit('limpiarPat');
              this.dialogDeleteExamPat = false;
             this.ListarMuestras();
          })
          .catch(err=>{ 
                this.ListarMuestras();
                err.response.data.sqlMessage? this.mensajeResultadoTransaccion=err.response.data.sqlMessage :   this.mensajeResultadoTransaccion=err;            
                setTimeout(()=>{this.mensajeResultadoTransaccion=''},2000 )
                 })
      this.dialogLaboratorio = false;
    },
    editarDataPat(data){
      this.$emit('fillDataModificarPat',data);
    },
    ListarMuestras() {
       var headers = {
        "Content-Type": "application/json",
        token: this.token,
       };

       this.PatologiaData=[];
       this.dataDetalleExam= [];
       this.dataDialogDetallePat= {
        nombre: "",
        data:[]
        };
        this.axios
        .post("/listarExamenPatologia",{desde:this.desde,hasta:this.hasta},{headers})
        .then((res) => {

          var examen = "";
          var dni="";
          //variable que contendra el dato de los examenes en el array para mostrar en  el dialog 
          var contTempDataDialog=[];
          var dataTemp = res.data;
          for (let i = 0; i < res.data.length; i++) {
             dni= dataTemp[0].dni;
             //primero guardamos los datos que no son igual a dni A
            var dataTempNoValido = dataTemp.filter((e) => {
              if (e.dni != dni) return e;
            });
            //sacamos los datos con dni A
            dataTemp = dataTemp.filter((e) => {
              if (e.dni == dni) return e;
            });
            //concatenamos los datos de los examenes para imprimirlos en su solo dato
            for (let j = 0; j < dataTemp.length; j++){
              //guardamos los datos de la persona con DNI A para mostrarlo en un dialog despues
                contTempDataDialog.push(dataTemp[j])
              if (j == 0) {
                examen = dataTemp[j].examen+'('+dataTemp[j].cantidad+')';
                continue;
              }
              examen = examen+", " + dataTemp[j].examen+'('+dataTemp[j].cantidad+')';
            }
            this.dataDetalleExam.push({index:i+1,id:dataTemp[0].idPaciente,idExamen:dataTemp[0].idExamen,empresa:dataTemp[0].empresa, dni: dni,
             paciente:dataTemp[0].nombres +' '+ dataTemp[0].apellidos,nombres:dataTemp[0].nombres ,apellidos:dataTemp[0].apellidos,
            fechNac:this.convertidorFecha(dataTemp[0].fechaNacimiento),telefono:dataTemp[0].telefono,
             examenes: examen,detalleExamen:contTempDataDialog,
             idTipoMuestraPat:dataTemp[0].idTipoMuestraPat,idMuestraPat:dataTemp[0].idMuestraPat});
            dataTemp = dataTempNoValido;
            examen = "";
            dni="";
            contTempDataDialog=[];
            if(dataTemp.length==0)
                break;
          }
           this.loading=false;
             this.PatologiaData=this.dataDetalleExam;
        })
        .catch((err) => {
          console.log(err);
        });
          },
  },
};
</script>